import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { useWebsiteInfoGetByInstitute } from "../../../../hooks/setting-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddSiteAcademicCoursesDetail from "./AddSiteAcademicCoursesDetail";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import CustomSquareTabCard from "../../../Ui/Tab/Square/CustomSquareTabCard";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import { useTranslation } from "react-i18next";

const SiteAcademicCoursesDetail = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();

  const { websiteInfoGetByInstitute, websiteInfoGetByInstituteRefetch } =
    useWebsiteInfoGetByInstitute({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) websiteInfoGetByInstituteRefetch();
  }, [instituteId, websiteInfoGetByInstituteRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  let measure = document.getElementById("562nbhghss")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  //
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.course?.head_name ?? ""}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {!searchParams.get("n") ? (
            <SquareTabWrapper
              dynamicId="562nbhghss"
              customStyle={{
                padding: "0",
              }}
            >
              {websiteInfoGetByInstitute?.one_ins?.landing_control?.academic_courses_desk?.map(
                (course) =>
                  `${course?._id}` === `${getQuery.state?.course?._id}`
                    ? course?.sub_head?.map((ct) => (
                        <CustomSquareTabCard
                          key={ct?._id}
                          label={ct?.head_name}
                          iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
                          url={`/q/${params.username}/other/site?type=academic-courses&n=one-detail`}
                          state={{
                            headId: course?._id,
                          }}
                          customStyleContainer={{
                            height: height,
                            width: width,
                          }}
                        />
                      ))
                    : null
              )}
            </SquareTabWrapper>
          ) : null}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {/* {disabled && <QvipleLoader />} */}
      {add && (
        <AddSiteAcademicCoursesDetail
          onClose={onToggle}
          onRefetch={websiteInfoGetByInstituteRefetch}
          acid={getQuery.state?.course?._id}
        />
      )}
    </>
  );
};

export default SiteAcademicCoursesDetail;
